.control {
  align-self: stretch;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  padding: 0 20px;
  border-left: 1px solid $color-night-rider;
  box-sizing: border-box;
  transition: background-color 0.6s ease;

  @include vp-1919 {
    padding: 0 15px;
  }

  &--login {
    max-width: 122px;

    @include vp-1279 {
      max-width: none;
    }
  }

  &--header {
    &:hover {
      background-color: $color-zambezi;
    }

    &:active {
      background-color: $color-night-rider;
    }
  }

  &--mobile {
    @include vp-1279 {
      min-height: 29px;
      margin-bottom: 15px;
      padding: 0;
      border: none;
    }
  }
}

.control--region.control--footer {
  padding: 0;
}

.control--phone.control--footer {
  padding: 0;
  border: none;
}

.control--email.control--footer {
  padding: 0;
  border: none;
}

//   

.control__icon {
  padding-right: 10px;
  line-height: 0;
}

.control--region .control__icon,
.control--login .control__icon {
  @include vp-1919 {
    padding-right: 0;
  }
}

.control--region.control--footer .control__icon {
  display: none;
}

.control--phone.control--footer svg {
  fill: $color-default-white;
}

.control--email.control--footer svg {
  width: 24px;
  height: 17px;
  fill: $color-default-white;
}

.control__icon svg {
  color: $color-alizarin-red;
  width: 16px;
  height: 24px;
}
