.video {
  iframe {
    width: 600px;
    height: 400px;
    margin: 0 auto;

    display: block;

    @include vp-1439 {
      width: 500px;
      height: 300px;
    }

    @include vp-1023 {
      width: 400px;
      height: 300px;
    }

    @media (max-width: 500px) {
      width: 266px;
      height: 190px;
    }
  }
}
