.tabs-page {
  .tabs {
    margin-top: -45px;

    @include vp-767 {
      margin-top: -35px;
    }
  }

  .main-header {
    padding-bottom: 25px;
  }

  .grid {
    padding-bottom: 4px;
  }

  .grid--news {
    margin-top: 30px;

    @include vp-1279 {
      margin-top: 20px;
    }
  }

  .pagination-numb {
    margin: 60px auto 80px;

    @include vp-1023 {
      margin-bottom: 60px;
    }

    @include vp-767 {
      margin-bottom: 40px;
    }
  }

  .content {
    margin-bottom: 85px;
    width: 870px;

    @include vp-1919 {
      margin-bottom: 75px;
      width: calc(50vw);
    }

    @include vp-1023 {
      margin-bottom: 45px;
      width: 100%;
    }
  }

  .gallery-slider {
    margin: 30px 0;
  }

  &__img {
    position: relative;
    margin-bottom: 20px;

    width: 100%;
    height: 620px;

    @include vp-1919 {
      height: 580px;
    }

    @include vp-1439 {
      height: 440px;
    }

    @include vp-1023 {
      margin-bottom: 10px;

      order: -1;
      height: 380px;
    }

    @include vp-767 {
      height: 260px;
    }

    &::after {
      content: "";
      position: absolute;
      bottom: -20px;
      left: 0;
      width: 100%;
      height: 20px;
      background-color: $color-alizarin-red;

      @include vp-1023 {
        bottom: -10px;
        height: 10px;
      }
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
