// 
//  

.header {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 500;
  display: flex;
  min-height: 64px;
  border-bottom: 4px solid $color-alizarin-red;
  background-color: $color-default-black;

  @include vp-1919 {
    min-height: 54px;
  }
}

//  

.header__inner {
  display: flex;
  align-items: center;
  align-self: stretch;
}

// 

.header__logo {
  margin-right: 20px;
  margin-left: 20px;

  @include vp-1919 {
    margin-right: 30px;
    margin-left: 15px;
  }

  @include vp-1439 {
    margin-right: 28px;
  }

  @include vp-1279 {
    margin-right: auto;
  }
}

//    (   )

.header__nav-toggle {
  display: none;

  @include vp-1279 {
    align-self: stretch;
    display: block;
    width: 50px;
    padding: 0;
    border: none;
    outline: none;
    background-color: $color-default-white;
    cursor: pointer;
  }
}

.header__nav-toggle svg {
  width: 32px;
  height: 16px;
  color: $color-alizarin-red;
}

//   ()

.header__nav {
  max-width: 875px;
  margin-left: auto;
  margin-right: 44px;

  @include vp-1919 {
    max-width: 810px;
    margin-left: 0;
    margin-right: auto;
  }

  @include vp-1439 {
    max-width: 687px;
  }

  @include vp-1279 {
    max-width: none;
  }
}

//   -  

.header__controls {
  display: flex;
  align-items: center;
  align-self: stretch;

  @include vp-767 {
    display: none;
  }
}

//  

.header__search {
  position: relative;
  align-self: stretch;
  display: block;
  max-width: 128px;
  padding-left: 55px;
  padding-right: 31px;
  background-color: $color-default-white;
  border: none;
  border-left: 1px solid $color-night-rider;
  outline: none;
  box-sizing: border-box;
  font-size: 12px;
  line-height: 15px;
  font-weight: 600;
  text-align: left;
  color: $color-zambezi;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    top: 18px;
    left: 18px;
    display: block;
    width: 26px;
    height: 25px;
    background: url("../img/svg/icon-search.svg") no-repeat center center;
    background-color: transparent;

    @include vp-1919 {
      top: 14px;
      left: 14px;
      width: 20px;
      height: 20px;
    }
  }

  @include vp-1919 {
    width: 50px;
    padding: 0;
    font-size: 0;
  }

  @include vp-767 {
    display: none;
  }
}

//    

.header__callback {
  position: relative;
  align-self: stretch;
  display: block;
  max-width: 138px;
  padding-left: 50px;
  padding-right: 20px;
  background-color: $color-alizarin-red;
  border: none;
  outline: none;
  box-sizing: border-box;
  font-size: 12px;
  line-height: 15px;
  font-weight: 600;
  text-align: left;
  color: $color-default-white;
  cursor: pointer;
  transition: opacity 0.6s ease;

  &::before {
    content: "";
    position: absolute;
    top: 18px;
    left: 20px;
    display: block;
    width: 22px;
    height: 24px;
    background: url("../img/svg/icon-callback.svg") no-repeat center center;
    background-color: transparent;

    @include vp-1919 {
      top: 13px;
      left: 15px;
    }
  }

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.7;
  }

  @include vp-1919 {
    max-width: 128px;
    padding-left: 46px;
  }

  @include vp-1439 {
    max-width: 116px;
    font-size: 10px;
    line-height: 12px;
  }
}
